.full-height-Reg {
    height: 95vh;
  }
  body {
    background-color: var(--form-box-color); /* Set the background color of the body to light gray */
  }
  .center-rectangle-Reg {
    background-color: var(--form-box-color); /* Set the background color of the rectangle to white */
    padding: 80px; /* Increase the padding to make the rectangle bigger */
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
    width: 800px; /* Set the width of the rectangle */
    max-width: 90%; /* Ensure the rectangle doesn't exceed 90% of the viewport width */
  }
  
  .center-rectangle-Reg h2 {
    margin-bottom: 5vh;
    font-size: 32px; /* Increase the font size of the heading */
    color: var(--text-color);
  }
  
  .center-rectangle-Reg input,.center-rectangle-Reg button {
    font-size: 18px; /* Increase the font size of the inputs and buttons */
    padding: 10px; /* Increase the padding of the inputs and buttons */
    width: 200px
  }
  #registerbtn{
    margin-top: 18px; 
    font-size: 22px;
    height: 50px;
    width: 200px;
  }
  

  

  
  